html, body {
    height: 100%;
    margin: 0;
}

.App {
    text-align: center;
}

.App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
    height: 80px;
}

@-webkit-keyframes App-logo-spin {
    from {
        -webkit-transform: rotate(0deg);
                transform: rotate(0deg);
    }
    to {
        -webkit-transform: rotate(360deg);
                transform: rotate(360deg);
    }
}

@keyframes App-logo-spin {
    from {
        -webkit-transform: rotate(0deg);
                transform: rotate(0deg);
    }
    to {
        -webkit-transform: rotate(360deg);
                transform: rotate(360deg);
    }
}

ul {
    margin: 0;
    padding: 0;
}

ul li {
    margin: 0;
    padding: 0;
    list-style: none;
}

.full-screen {
    background: #ffffff;
    width: 100%;
    height: 100%;
}

.content-header {
    padding: 12px;
    margin: 0 auto;
    text-align: center;
    color: rgb(222, 102, 56);
}

.content-padded {
    padding: 24px;
}

.flex-vertical {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    height: 100%;
}

.height-two-thirds {
    -ms-flex: 2 1;
        flex: 2 1;
}

.height-one-third {
    -ms-flex: 1 1;
        flex: 1 1;
}

.station-information__content {
    -ms-flex-positive: 1;
        flex-grow: 1;
    background: url(https://upload.wikimedia.org/wikipedia/commons/3/36/Fru%C3%A4ngen_2010a.jpg);
    text-align: center;
}

.station-information__minimap {
    -ms-flex: 1 1;
        flex: 1 1;
    background: #000;
}

.station-information__toolbar {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: row;
        flex-direction: row;
}

.station-information__toolbar > a {
    -ms-flex: 1 1;
        flex: 1 1;
}

.station-information__toolbar__icon {
    -ms-flex: 1 1;
        flex: 1 1;

    display: -ms-flexbox;

    display: flex;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-align: center;
        align-items: center;

    margin: 12px;
    padding: 4px;
    background: #ffffff;

    font-size: 2em;
    color: #000000;

}

.go-back {
    text-align: right;
    padding: 8px;
    text-decoration: none;
}

.map-text {
    font: 12pt sans-serif;
}

.map-text-bold {
    font: bold 12pt sans-serif;
}

table {
    margin: 0 auto;
    border: 1px solid aliceblue;
    padding: 8px;
    border-collapse: collapse;
}

table tr {
    padding: 8px;
}

table tr td {
    border: 1px solid coral;
    padding: 8px;
}

.box {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-flow: column;
        flex-flow: column;
    height: 100%;
}

.box .row {
    border: 1px dotted grey;
}

.box .row.header {
    -ms-flex: 0 1 auto;
        flex: 0 1 auto;
}

.box .row.content {
    -ms-flex: 1 1 auto;
        flex: 1 1 auto;
}

.box .row.footer {
    -ms-flex: 0 1 40px;
        flex: 0 1 40px;
}

/* component styles */
.legend {
    position: absolute;
    bottom: 0;
    padding: 20px;
}



